.contentProduct {
  background-color: #ffffff;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 200px;
  padding: 15px;
}

.contentProduct img {
  border-radius: 5px;
  height: 200px;
  width: 200px;
}

.contentInfoProducto {
  text-align: left;
  padding-left: 15px;
}

.title {
  font-size: 20px;
  cursor: pointer;
  margin-bottom: 10px;
}

.description {
  width: 450px;
  font-size: 15px;
}
